import jQuery from 'jquery';

function fetchSingleEntity(entityName, mvcController) {
    return new Promise((executor, reject) => {
        const requestUrl = `${mvcController}/getsingle`
        $.ajax({
            url: requestUrl,
            data: { entityName },
            method: "GET",
            success: (entityResponse) => {
                if (__DEV__) {
                    console.warn('fetchSingleEntity was called and responded:')
                    console.log(entityResponse)
                }
                executor(entityResponse)
            }
        })
    })
}

function fetchPage(entityName) {
    return new Promise((executor, reject) => {
        const requestUrl = `/page/getsingle`
        $.ajax({
            url: requestUrl,
            data: { entityName },
            method: "GET",
            success: (entityResponse) => {
                if (__DEV__) {
                    console.warn('fetchSingleEntity was called and responded:')
                    console.log(entityResponse)
                }
                executor(entityResponse)
            }
        })
    })
}

export { fetchPage, fetchSingleEntity }