import TrangChu from './trang-chu/index'
import VeChungToi from './gioi-thieu/index'
import CongTrinh from './cong-trinh/index'
import BoSuuTap from './bo-suu-tap/index'
import DuAn from './du-an/index'
import ThuVien from './thu-vien/index'
import HoiDap from './hoi-dap/index'
import LienHe from './lien-he/index'

const DEFAULT_MENU = 'PRIMARY'
const INIT_ROUTES = 'INIT_ROUTES'
const REFRESH_ROUTE_PATH = 'REFRESG_ROUTE_PATH'

const getRoutes = () => ({
    exact: true,
    path: '/',
    name: 'trang-chu',
    label: localizationString.getString('Trang chủ'),
    menuOrders: {
        [ DEFAULT_MENU ]: 1,
        footer: 3
    },
    component: TrangChu,
    childRoutes: [
        {
            exact: true,
            path: '/ve-chung-toi',
            name: 've-chung-toi',
            defaultLabel: localizationString.getString('Về chúng tôi'),
            menuOrders: {
                [ DEFAULT_MENU ]: 2,
            },
            component: VeChungToi
        },
        {
            path: '/cong-trinh',
            name: 'cong-trinh',
            defaultLabel: localizationString.getString('Công trình'),
            menuOrders: {
                [ DEFAULT_MENU ]: 3,
            },
            component: CongTrinh,
            redirectToChild: 1,
            childRoutes: [
                {
                    path: '/chi-tiet/:entity',
                    name: 'cong-trinh-chi-tiet',
                },
                {
                    exact: false,
                    path: '/:category',
                    name: 'cong-trinh-category',
                    defaultLocation: '/tat-ca',
                }
            ]
        },
        {
            path: '/bo-suu-tap',
            name: 'bo-suu-tap',
            defaultLabel: localizationString.getString('Bộ sưu tập'),
            menuOrders: {
                [ DEFAULT_MENU ]: 4,
            },
            component: BoSuuTap,
            redirectToChild: 1,
            childRoutes: [
                {
                    path: '/chi-tiet/:entity',
                    name: 'bo-suu-tap-chi-tiet',
                },
                {
                    exact: false,
                    path: '/:category',
                    name: 'bo-suu-tap-category',
                    defaultLocation: '/tat-ca',
                }
            ]
        },
        {
            path: '/du-an',
            name: 'du-an',
            defaultLabel: localizationString.getString('Dự án'),
            menuOrders: {
                [ DEFAULT_MENU ]: 5,
            },
            component: DuAn,
            redirectToChild: 1,
            childRoutes: [
                {
                    path: '/du-an/ban-do/:category',
                    name: 'ban-do-du-an',
                },
                {
                    path: '/du-an/loai-cong-trinh/:category',
                    name: 'du-an-category',
                    defaultLocation: '/loai-cong-trinh/tat-ca',
                },
                {
                    path: '/du-an/chi-tiet/:du-an',
                    name: 'du-an-chi-tiet',
                }
            ]
        },
        {
            path: '/thu-vien',
            name: 'thu-vien',
            redirectToChild: 1,
            defaultLabel: localizationString.getString('Thư viện'),
            component: ThuVien,
            menuOrders: {
                [ DEFAULT_MENU ]: 6,
            },
            childRoutes: [
                {
                    path: '/chi-tiet/:blog',
                    name: 'thu-vien-chi-tiet',
                },
                {
                    exact: false,
                    path: '/:category',
                    name: 'thu-vien-category',
                    defaultLocation: '/tat-ca',
                }
            ]
        },
        {
            path: '/hoi-dap',
            name: 'hoi-dap',
            defaultLabel: localizationString.getString('Hỏi đáp'),
            menuOrders: {
                [ DEFAULT_MENU ]: 7,
                footer: 2
            },
            component: HoiDap
        },
        {
            path: '/lien-he',
            name: 'lien-he',
            defaultLabel: localizationString.getString('Liên hệ'),
            menuOrders: {
                [ DEFAULT_MENU ]: 8,
            },
            component: LienHe
        },
        {
            path: '/phuong-phap',
            name: 'phuong-phap',
            defaultLabel: localizationString.getString('Liên hệ'),
            menuOrders: {
                footer: 1
            },
            component: LienHe
        },
    ]
})

const popularMenusFromRoute = (menus, route) => {
    let { exact, path, label, defaultLabel, menuOrders, childRoutes } = route

    //Nếu menuOrders không được định nghĩa thì không thêm route vào menu
    if (!menuOrders)
        return

    for (var menuLocation in menuOrders) {

        //Tạo menu nếu trong route được định nghĩa menuOrders
        if (!menus[ menuLocation ])
            menus[ menuLocation ] = []

        //Thứ tự của 
        let menuOrder = menuOrders[ menuLocation ]

        menus[ menuLocation ].push({
            exact, url: path, label: label || defaultLabel, order: menuOrder
        })
    }

    return menus
}

const menuFormRootRoute = (rootRoute) => {
    //menus là một object tập hợp của nhiều menu, với property là têm menu và value và array menu item
    //Mặc định cho menu của app là 'PRIMARY' được định nghĩa bởi const 'DEFAULT_MENU'
    var menus = {
        [ DEFAULT_MENU ]: []
    }

    //tạo menu item cho  trang chủ (root route)
    menus = popularMenusFromRoute(menus, rootRoute);

    let { childRoutes } = rootRoute

    //tạo menu item cho các route con
    for (var childIndex in childRoutes) {
        menus = popularMenusFromRoute(menus, childRoutes[ childIndex ])
    }

    return menus
}

const getRoutePath = (routes = [], currentRouteName, labels) => {
    var resultRoutePath = []

    for (var routeIndex in routes) {
        var route = routes[ routeIndex ]

        if (labels && labels[ route.name ])
            route.label = labels[ route.name ]
        else
            route.label = route.defaultLabel
        if (route.name == currentRouteName) {
            resultRoutePath.push(route)
            return resultRoutePath
        } else if (route.childRoutes) {
            resultRoutePath.push(route)
            var nextRoute = getRoutePath(route.childRoutes, currentRouteName, labels);
            if (nextRoute.length) {
                resultRoutePath = resultRoutePath.concat(nextRoute)
                return resultRoutePath
            } else
                resultRoutePath = []
        } else {
            resultRoutePath = []
        }
    }

    return resultRoutePath
}


const refreshRoutePath = (currentRouteName, replaceRouteDefaultLabels) => ({
    type: REFRESH_ROUTE_PATH,
    currentRouteName,
    replaceRouteDefaultLabels
})

const reducer = (state = {}, action) => {
    switch (action.type) {
        case INIT_ROUTES:
            const routes = getRoutes()
            const menus = menuFormRootRoute(routes)

            const newState = { routes, menus }

            if (__DEV__) {
                console.warn(`State was changed! Action type: ${action.type}`)
                console.log(newState)
            }

            return newState

        case REFRESH_ROUTE_PATH:
            const routePath = getRoutePath(state.routes.childRoutes, action.currentRouteName, action.routeLabels);
            routePath.unshift(state.routes)
            return $.extend(true, {}, state, { routePath })
        default:
            return state
    }
}

export { reducer, refreshRoutePath, INIT_ROUTES, DEFAULT_MENU }